import { IgcContentPaneComponent } from './igc-content-pane-component';
import { ContentPane } from './ContentPane';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcContentPaneModule {
    public static register(): void {
        IgcContentPaneComponent.register();
        TypeRegistrar.registerCons("IgcContentPaneComponent", IgcContentPaneComponent);
        TypeRegistrar.register("ContentPane", (<any>ContentPane).$type);
    }
}
