import { IgcDocumentHostComponent } from './igc-document-host-component';
import { DocumentHost } from './DocumentHost';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcDocumentHostModule {
    public static register(): void {
        IgcDocumentHostComponent.register();
        TypeRegistrar.registerCons("IgcDocumentHostComponent", IgcDocumentHostComponent);
        TypeRegistrar.register("DocumentHost", (<any>DocumentHost).$type);
    }
}
