import { IgcSplitPaneComponent } from './igc-split-pane-component';
import { SplitPane } from './SplitPane';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcSplitPaneModule {
    public static register(): void {
        IgcSplitPaneComponent.register();
        TypeRegistrar.registerCons("IgcSplitPaneComponent", IgcSplitPaneComponent);
        TypeRegistrar.register("SplitPane", (<any>SplitPane).$type);
    }
}
